import React, { useEffect, useState } from "react";

function Portfolio(props) {
  const buildings = [1, 2, 3, 4, 5, 6, 7, 8];
  const houses = [1, 2, 3, 4, 5];
  const ports = [1, 2, 3, 4, 5];

  let images = [];
  houses.forEach((i) => {
    images.push(`house${i}.jpg`);
  });
  buildings.forEach((i) => {
    images.push(`building${i}.jpg`);
  });
  ports.forEach((i) => {
    images.push(`port${i}.jpg`);
  });

  const [imgIndex, setImgIndex] = useState(0);
  const [imgReady, setImageReady] = useState(false);

  useEffect(() => {
    const timer = setInterval(() => {
      if (!imgReady) {
        return;
      }

      let newIdx = imgIndex + 1;
      if (newIdx === images.length) {
        newIdx = 0;
      }
      setImageReady(false);
      setImgIndex(newIdx);
    }, 8000);

    return () => clearInterval(timer);
  }, [imgIndex, imgReady]);

  function moveNext() {
    let newIdx = imgIndex + 1;
    if (newIdx === images.length) {
      newIdx = 0;
    }
    setImageReady(false);
    setImgIndex(newIdx);
  }

  function movePrev() {
    let newIdx = imgIndex - 1;
    if (newIdx === -1) {
      newIdx = images.length - 1;
    }
    setImageReady(false);
    setImgIndex(newIdx);
  }

  let imgTimer;
  function imageReady() {
    // clearTimeout(imgTimer);
    // imgTimer = setTimeout(() => {
    //   setImageReady(true);
    // }, 500);
    setImageReady(true);
  }

  return (
    <div className="portfolio">
      <h3 style={{ zIndex: 1 }}>
        <img
          src="../images/minibox.png"
          alt=""
          style={{ height: "25px", marginRight: "5px" }}
        />
        &nbsp;Portfolio
      </h3>
      <div style={{ zIndex: 1 }} className="imgNav">
        <div onClick={movePrev}>
          <i className="fa fa-chevron-left"></i>
        </div>
        <div>{imgReady ? "" : "loading..."}</div>
        <div onClick={moveNext}>
          <i className="fa fa-chevron-right"></i>
        </div>
      </div>
      <div
        className={"imgPreview " + (imgReady ? "imgReady" : "")}
        style={{
          backgroundImage: imgReady
            ? `url(../portfolio/${images[imgIndex]})`
            : "none",
        }}
      >
        <img
          style={{ height: "10px", opacity: 0 }}
          src={`../portfolio/${images[imgIndex]}`}
          onLoad={() => imageReady()}
        ></img>
      </div>
    </div>
  );
}

export default Portfolio;
